.profile {
    width: 300px;
    height: 440px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    margin-top: 50px;
    margin-bottom: 50px;
    border: 1px solid #eee;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.avatar {
    width: 150px;
    height: 150px;
    display: block;
    margin-top: 40px;
    margin-left: auto;
    margin-bottom: 30px;
    margin-right: auto;
    border-radius: 50%;
    border: 1px solid dimgray;
}

.name{
    margin-top: 0px;
    margin-bottom: 15px;
    text-align: center;
    font-size: 30px;
    font-weight: 700;
}

.tag {
    margin-top: 0px;
    margin-bottom: 15px;
    text-align: center;
    font-size: 20px;
    color: dimgray;
  }
  
.location {
    margin-top: 0px;
    margin-bottom: 30px;
    text-align: center;
    font-size: 20px;
    color: dimgray;
}

.stats {
    display: flex;
    justify-content: space-evenly;
    padding: 0;
    margin: 0;
    list-style: none;
    background-color: #F3EDED;
    border-top: 1px solid #CAC6C6;
    border-radius: 10px;
}

.item {
    width: 100px;
    height: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.item:not(:last-child) {
    border-right: 1px solid #CAC6C6;
}

.label {
    margin-bottom: 5px;
    font-size: 16px;
    color: dimgray;
}

.quantity {
    font-size: 16px;
    font-weight: 700;
}